<template>
    <section>
        <div class="col-md-12 border pt-3">
            <div class="col-md-12 p-0  mb-3">
                <dynamic-form lang="en" ref="validateRuleConfig" :buttons="buttons" :mandatorytext="$t('MandatoryString')" :schema="FormSchema"
                    v-on:OnSubmit="OnSubmit">
                </dynamic-form>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from "../../../services/DataService";

export default {
    props: {
        configurationId: {
            type: String,
            required: false
        },
        serviceId: {
            type: String,
            required: false
        },
        reqType: {
            type: String,
            required: false
        }
    },
    data() {
        return {
            isLoading: false,
            ruleServiceList:[],
            FormSchema: [
                {
                    layoutType: "four",
                    Data: [
                       {
                            astype: "SelectField",
                            label: this.$t('Service'),
                            name: "Service",
                            value: "",
                            placeholder: this.$t('Select'),
                            visibility: true,
                            config: {
                                options: [],
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('BillingRuleName'),
                            name: "BillingRuleName",
                            value: "",
                            placeholder: '',
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EffectiveFromDate'),
                            name: "EffectiveFromDate",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                //minDate: new Date(),
                                onChange: this.setTimerDate,
                                maxDate: false
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EffectiveToDate'),
                            name: "EffectiveToDate",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                minDate: new Date(),
                                // onChange: this.setTimerDate,
                                maxDate: false
                            },
                            validationRules: { "required": true },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('DESCRIPTION'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            config: {
                                rows: 5
                            },
                            validationRules: { "required": false, max: 2000 },
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('SaveAndNextWizard'),
                    isVisible: true,

                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>" + this.$t('Cancel'),
                    onClick: this.Close,
                }
            ],
            mode :false
        }
    },
    created: async function () {
        let vm = this;
        if(this.$route.params.mode =='View'){
            vm.mode = true;
        }
        if (vm?.configurationId) {
            await vm.BindConfigurationData(vm.configurationId);
        }else{
            const date = new Date(2099, 11, 31);
            const formattedDate = `${date.getMonth() + 1}/${date.getDate()}/${date.getFullYear()}`;
            await vm.$nextTick();
            vm.$refs.validateRuleConfig.updateProperty("EffectiveToDate", "value", formattedDate)
            vm.$refs.validateRuleConfig.UpdateKeyValue();
        }
        await vm.GetDDLBillingRuleService();
        //  If in view mode, disable all fields
        if (vm.mode) {
            vm.FormSchema.forEach(section => {
                section.Data.forEach(field => {
                    field.disabled = true; // or field.readonly = true based on your form component
                });
            });
             //  Update buttons in View mode
            vm.buttons.forEach(btn => {
                if (btn.type === 'submit') {
                    // Hide or disable the Save button
                    btn.isVisible = false; // or btn.disabled = true if you want it visible but inactive
                }
            });
        }
    },
    methods: {
        Close() {
            var vm = this;
            if(vm.reqType == 'fromServiceMapping'){
                this.$parent.$parent.$parent.isModalBillingConfiguration=false;
                this.$parent.$parent.$parent.FetchData();
            }
            else
            {
                vm.$router.push({ name: 'BillingRuleListing' });
            }
        },
        GetDDLBillingRuleService: async function () {
            var vm = this;
            var params = `id=&moduleName=&type=BILLING_RULE_SERVICE_NAME&search=`
            await DataService.GetDDLData(params).then((response) => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.$refs.validateRuleConfig.updateProperty("Service", "config.options", response.data.DATA);
                        vm.$refs.validateRuleConfig.UpdateKeyValue();
                        var serviceId ='';
                        if(this.reqType == 'fromServiceMapping'){
                            serviceId = this.serviceId;
                        }
                        else{
                           serviceId = parseInt(vm.$route.params.serviceId);
                        }
                        if(serviceId != 0){
                            vm.$refs.validateRuleConfig.updateProperty("Service", "value", serviceId);
                            vm.$refs.validateRuleConfig.updateProperty("Service", "disabled", true);
                            vm.$refs.validateRuleConfig.UpdateKeyValue();
                        }
                    }
                }
            })
        },
        BindConfigurationData: async function (configurationId) {
            var vm = this;
            const url = `configId=${configurationId}`;
            const response = await DataService.GetConfigurationData(url);
            if (response && response.data && response.data.length > 0) {
                var configurationValue = response.data[0];
                vm.$refs.validateRuleConfig.updateProperty("BillingRuleName", "value", configurationValue.RULE_NAME);
                vm.$refs.validateRuleConfig.updateProperty("EffectiveFromDate", "value", configurationValue.EFFICATIVE_DATE_FROM);
                vm.$refs.validateRuleConfig.updateProperty("EffectiveToDate", "value", configurationValue.EFFICATIVE_DATE_TO);
                vm.$refs.validateRuleConfig.updateProperty("Description", "value", configurationValue.DESCRIPTION);
                vm.$refs.validateRuleConfig.updateProperty("Service", "value", configurationValue.BILLING_SERVICE_ID);
                vm.$refs.validateRuleConfig.updateProperty("Service", "disabled", true);
                vm.$refs.validateRuleConfig.UpdateKeyValue();
            }
        },
        setTimerDate: function (x, y, z) {
            var vm = this;
            if (y.name == "EffectiveFromDate") {
                vm.$refs.validateRuleConfig.updateProperty("EffectiveToDate", "config.minDate", x);
                vm.$refs.validateRuleConfig.UpdateKeyValue();
            }
        },

        async OnSubmit(formfields, val) {
            var vm = this;
            var jsonObj = {
                configurationId: vm?.configurationId ? vm.configurationId :0,
                billingRuleName: formfields.BillingRuleName,
                effectiveFromDate: formfields.EffectiveFromDate,
                effectiveToDate: formfields.EffectiveToDate,
                description: formfields.Description,
                billingServiceId : formfields.Service
            };
            var params = JSON.stringify(jsonObj);
            DataService.SaveBillingRuleConfiguration(params).then((response) => {
                if (response.data.status == "Success" && response.data.code == "1") {
                    vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Configuration")]), "success", true, vm.$t("Alert"));
                    setTimeout(function () {
                        if(vm.reqType !== 'fromServiceMapping'){
                            vm.$router.push({ name: 'ManageConfiguration', params: { id: respoens.data.result,serviceId : response.data.linkedId } });
                        }
                        vm.$emit('config-saved', { configId: response.data.result, goTo: 'NodeConfiguration' });
                    }, 200);
                }
                else if (response.data.status == "Success" && response.data.code == "2") {
                    vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Configuration")]), "success", true, vm.$t("Alert"));
                }
                else if (response.data.status == "Success" && response.data.code == "3") {
                    vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Configuration')]), "failure", true, vm.$t('Alert'));
                }
            });
        },
        close: function () {

        }
    }
}
</script>