<template>
    <section>
        <div>
            <loader :is-visible="isLoading"></loader>
            <tg-list :IsShowAction="true" :showCheckBox="true"   :mode="mode"  ModuleName="RuleEngine" :listType="['List']"
                :HeaderButtons="listheaderbuttons" :HeaderText="$t('YellowFlagTitle')"
                :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick"
                SubModuleCode="RuleEngine" IdentityColumn="limit_id" :ListData="LimitData" :HeaderData="Headers"
                :callbackfunction="FetchData" v-on:HeaderButtonClick="commonHeaderButtonClick"
                :TotalRecords="TotalRecords" :ListDataCallBackFunction="FetchData" :LegendArray="[]" :SortExp="sortExp"
                :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod"
                v-on:SortdataButtonClick="sortdata"></tg-list>
        </div>
        <AddLimit v-if="isAddLimit" :callbackfunction="FetchData" :refCode="refCode" @CloseLimit="CloseLimit"
            :configurationId="this.configurationId" :EditLimitData="EditLimitData">
        </AddLimit>
    </section>
</template>
<script>

import DataService from '../../../services/DataService';
import AddLimit from './AddLimit.vue'
export default {
    components: { AddLimit },
    props: {
        configurationId: {
            type: String,
            default: null
        },
         mode: {
    type: String,
    default: 'Edit'
  },
    },
    data() {
        return {
            EditLimitData: [],
            isLoading: false,
            refCode: '',
            isAddLimit: false,
            noRecord: true,
            LimitData: [],
            TotalRecords: 0,
            ConfigId: 0,
            LimitId: 0,
            ClientId: 0,
            Sort_By: '',
            Sort_Exp: '',
            PageSize: 10,
            PageNumber: 1,
            Headers: [
                // {
                //     COLUMN_NAME: "billing_config_name",
                //     DATA_TYPE: "text",
                //     SORTABLE: false,
                //     VISIBLE: true,
                //     DISPLAY_NAME: "Limit For",
                //     DISPLAY_ORDER: 1,
                // },
                {
                    COLUMN_NAME: "work_code",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('WorkCode'),
                    DISPLAY_ORDER: 2,
                },
                {
                    COLUMN_NAME: "limit",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('LimitValue'),
                    DISPLAY_ORDER: 3,
                },
                {
                    COLUMN_NAME: "limit_days",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('DayLimit'),
                    DISPLAY_ORDER: 3,
                },
                {
                    COLUMN_NAME: "alternate_code",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('AlternateWorkCode'),
                    DISPLAY_ORDER: 3,
                },

                {
                    COLUMN_NAME: "display_order",
                    DATA_TYPE: "number",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('DisplayOrder'),
                    DISPLAY_ORDER: 4,
                }

            ],
            listheaderbuttons:
                [
                    {
                        title: 'AddNew',
                        iconClass: 'fa fa-plus',
                        callbackfunction: 'Add',
                        additionalClass: '',
                        isdisabled: false,
                        isvisible: true,
                        checkPrivilege: false,
                    },
                    {
                        title: 'Delete',
                        iconClass: 'fa fa-trash',
                        callbackfunction: 'Delete',
                        additionalClass: '',
                        isdisabled: true,
                        isvisible: true,
                        checkPrivilege: false,
                        isEnabledOnSelectedRow: true
                    },
                    {
                        title: 'BacktoList',
                        iconClass: 'fa fa-angle-double-left',
                        callbackfunction: 'BackToList',
                        additionalClass: '',
                        isdisabled: false,
                        isvisible: true,
                        checkPrivilege: false
                    }
                ],
            listActions: [
                {
                    title: 'Edit',
                    callbackfunction: 'EditRecord',
                    icon: 'fa fa-pencil text-success action-icon',
                    additionalSpan: '',
                    
                }
            ],
        }
    },
    mounted() {
        debugger
    this.setHeaderButtons(this.mode);
},
    async created() {
        var ths = this;
        await ths.FetchData();
    },
    methods: {
        setHeaderButtons(mode) {
    debugger;

    if (mode === 'View') {
        this.listheaderbuttons = this.listheaderbuttons.map(btn => {
            if (btn.title === 'BacktoList') {
                return { ...btn, isvisible: true, isdisabled: false };
            }
            return { ...btn, isvisible: false, isdisabled: true };
        });
    }

    else if (mode === 'Edit') {
        this.listheaderbuttons = this.listheaderbuttons.map(btn => {
            let updatedBtn = { ...btn };

            updatedBtn.isvisible = true;

            if (btn.title === 'Delete' && btn.isEnabledOnSelectedRow) {
                updatedBtn.isdisabled = true;  
            } 
            else {
                updatedBtn.isdisabled = false; 
            }

            return updatedBtn;
        });
    }
},


        CloseLimit() {
            this.EditLimitData = [];
            this.refCode = '';
            this.isAddLimit = false;
        },
        FetchData() {
            var vm = this;
            vm.isLoading = true;
            if (this.configurationId == null || this.configurationId == 0) {
                this.configurationId = 0;
            }
            var url = `configId=${vm.configurationId}&limitId=${vm.LimitId}&client_id=${vm.ClientId}&sort_by=${vm.Sort_By}sort_exp=${vm.Sort_Exp}&pagesize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
            DataService.GetLimitListing(url).then((response) => {
                if (response.data != '') {
                    if (response.data.length > 0) {
                        vm.LimitData = response.data;
                        vm.TotalRecords = vm.LimitData.length > 0 ? vm.LimitData[0].TOTALRECORDS : 0;
                        vm.noRecord = false;
                    }

                }
                else {
                    vm.LimitData = [];
                    vm.TotalRecords = 0;
                    vm.noRecord = true;
                }
            });
            vm.isLoading = false;
        },

        renderActions(row) {
            let rtnActionArray = [];
            this.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                action.isVisible = true;
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        actionButtonClick(callbackfunction, item, index, event, isChild) {
            switch (callbackfunction) {
                case 'EditRecord':
                    this.EditLimit(item);
                    break;
            }
        },
        EditLimit(item) {
            var vm = this;
            vm.EditLimitData = item;
            vm.refCode = 'Update';
            vm.isAddLimit = true;
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "Add":
                    this.AddLimitRecord();
                    break;
                case "BackToList":
                    this.BackToList();
                    break;
                case "Delete":
                    this.commonDeleteData();
            }
        },
        commonDeleteData: function (event) {
            debugger
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(async function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("LimitValue")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "BillingRule",
                        refCode: "TALYGEN_BE_billing_limits"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    debugger
                    DataService.CommonCommonDelete(objectPostString).then(async function (response) {
                        debugger
                        if (response.status == 200) {
                            $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                            $("#DeleteMultiple").addClass("disabled");
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('LimitValue')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('LimitValue')]), "failure", true, vm.$t("Alert"));
                        }

                    });

                });
                vm.FetchData();
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "failure", true, vm.$t("Alert"));
            }

        },
        BackToList() {
            var vm = this;
            vm.$router.push({ name: 'BillingRuleListing' });
        },
        AddLimitRecord() {
            this.isAddLimit = true;
        },
        pagerMethod(value) {
            debugger
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (SortBy) {
            this.SortBy = SortBy;
            var SortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                SortExp = 'DESC';
            }
            this.SortExp = SortExp;
            this.SortBy = SortBy;
            this.SortExp = SortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        },
        leftCommonSearch: async function (value) {
            debugger;
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (value.searchCondition != "" && typeof value.searchCondition != 'undefined') {
                let str = value.searchConditionJson;
                const regex = /(WORK_CODE|WORK_CODE_CATEGORY_ID)\s*(?:LIKE\s+'?%?|=)\s*([^%')\s]+).*?/i;
                const m = str.match(regex);
                const column = m[1];
                const values = m[2];
                switch (column) {
                    case "WORK_CODE":
                        this.searchCondition = values;
                        break;
                    case "WORK_CODE_CATEGORY_ID":
                        this.categoryId = values;
                        break;
                }
            }
            else {
                this.categoryId = "";
                this.searchCondition = "";
            }
            this.FetchData();
        }
    },

}


</script>

<style lang="scss" scoped></style>