<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div class="padding-t_10 p-0" id="reportstabsdata">
            <div class="left-menu-tab mt-2">
                <div class="col-md-12 p-0">
                    <div class="theme-primary partition">
                        <span class="p-name text-white">{{ $t("BillingRule") }}</span>
                    </div>
                </div>
                <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab  nav-tabs">
                    <!-- <li v-for="tab in tabs" :key="tab.id" @click="changeTab(tab)"
                        :class="{ 'nav-item': true, 'active': currentTab.title === tab.title }">
                        <span :class="{ 'nav-link left-menu': true, 'active': currentTab.title === tab.title }">
                            <i :class="tab.icon" class="p-1"></i> {{ tab.displayName }}</span>
                    </li> -->
                    <li
                        v-for="tab in tabs"
                        :key="tab.id"
                        @click="changeTab(tab)"
                        :class="{
                            'nav-item': true,
                            'active': currentTab.title === tab.title,
                            'disabled-tab': !tabsUnlocked && tab.title !== $t('BillingRuleConfiguration')
                        }"
                        :style="!tabsUnlocked && tab.title !== $t('BillingRuleConfiguration') ? 'pointer-events: none; opacity: 0.5;' : ''"
                    >
                        <span :class="{
                            'nav-link left-menu': true,
                            'active': currentTab.title === tab.title
                        }">
                            <i :class="tab.icon" class="p-1"></i> {{ tab.displayName }}
                        </span>
                    </li>
                </ul>

            </div>
            <div class="right-content">
                <!--class="right-content forms-for-billing-plandetails" -->
                <div class="tab-content" id="myTabContent">
                    <div class="expand-list">
                        <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                            <i class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></i>
                        </a>
                    </div>
                    <div id="ReturnRequestHeading" class="theme-primary partition  p-actions-expand mt-2"
                        v-if="currentTab.title != $t('LimitValue')">
                        <span class="p-name text-white"> {{ currentTab.displayName }}</span>
                        <!-- <RouterLink :to="reqFrom" title="" class="p-action-btn text-white float-right" v-if="reqFrom != null"
                            data-toggle-tooltip="tooltip" data-original-title="Back to list"><i
                                class="fa fa-angle-double-left pr-2"></i><br>{{ $t("BacktoList") }}</RouterLink> -->
                        <RouterLink to="/BillingRule/Index" title="" class="p-action-btn text-white float-right" v-if="reqType !== 'fromServiceMapping'"
                            data-toggle-tooltip="tooltip" data-original-title="Back to list"><i
                                class="fa fa-angle-double-left pr-2"></i><br>{{ $t("BacktoList") }}</RouterLink>

                       <a
                        @click="callAddNew"
                        title=""
                        class="p-action-btn text-white float-right"
                        v-if="currentTab.title == $t('OverrideCharge') && mode !== 'View'"
                        data-toggle-tooltip="tooltip"
                        data-original-title="Add"
                    >
                        <em class="fa fa-plus"></em><br>{{ $t("Add") }}
                    </a>

                    </div>  
                    <div class="col-md-12 p-0">
                        <BillingRuleConfiguration v-if="currentTab.title == $t('BillingRuleConfiguration')"
                            @config-saved="handleSavedConfig" :configurationId="configurationId" :serviceId="serviceId" :reqType="reqType">
                        </BillingRuleConfiguration>
                        <Limit v-if="currentTab.title == $t('LimitValue')" :mode="mode" :configurationId="configurationId"></Limit>
                        <NodeConfiguration v-if="currentTab.title == $t('NodeConfiguration')"
                            :configurationId="configurationId" :reqType="reqType" :serviceId="serviceId" :mode="mode"></NodeConfiguration>
                        <VerticalFlowChart v-if="currentTab.title == $t('FlowChart1')"
                            :configurationId="configurationId"></VerticalFlowChart>
                        <OverrideCharges v-if="currentTab.title == $t('OverrideCharge')" ref="overrideChargesRef"
                            :configurationId="configurationId" :mode="mode"></OverrideCharges>
                        <HorizontalFlowChart  v-if="currentTab.title == $t('FlowChart')"
                        :configurationId="configurationId"></HorizontalFlowChart>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import DataServices from "../../services/DataService";
import BillingRuleConfiguration from "./ConfigurationComponent/BillingRuleConfiguration.vue";
import Limit from "./ConfigurationComponent/Limit.vue";
import NodeConfiguration from "./ConfigurationComponent/NodeConfiguration.vue";
import VerticalFlowChart from "./ConfigurationComponent/VerticalFlowChart.vue";
import OverrideCharges from './ConfigurationComponent/OverrideCharges.vue';
import HorizontalFlowChart from "./ConfigurationComponent/HorizontalFlowChart.vue";
export default {
    components: { NodeConfiguration, Limit, BillingRuleConfiguration, VerticalFlowChart, OverrideCharges ,HorizontalFlowChart},
    props: {
        serviceId: {
            type: String,
            required: false
        },
        reqType: {
            type: String,
            required: false
        },
        configurationId :{
            type: String,
            required: false
        },
        mode :{
            type: String,
            default: 'Edit'

        }
    },
    data() {
        return {
            unlockedTabs: [1], 
            activeTab: 1,
            tabs: [{ title: this.$t('BillingRuleConfiguration'), displayName: this.$t('BillingRuleConfiguration'), icon: "fa fa-cogs", id: 1 },
            { title: this.$t('NodeConfiguration'), displayName: this.$t('NodeConfiguration'), icon: "fa fa-xing", id: 2 },
            { title: this.$t('LimitValue'), displayName: this.$t('LimitValue'), icon: "fa fa-circle-o-notch", id: 3 },
            { title: this.$t('OverrideCharge'), displayName: this.$t('OverrideCharge'), icon: "fa fa-deviantart", id: 4 },
            { title: this.$t('FlowChart1'), displayName: this.$t('FlowChart1'), icon: "fa fa-usb", id: 5 },
            { title: this.$t('FlowChart'), displayName: this.$t('FlowChart'), icon: "fa fa-sitemap", id: 6 },],
            currentTab: { title: this.$t('BillingRuleConfiguration'), displayName: this.$t('BillingRuleConfiguration'), icon: "fa fa-cogs", id: 1 },
            isLoading: false,
            configurationId: 0,
            // reqFrom:'',
            tabsUnlocked: false,
            mode: 'Edit'
        }
    },
    created: async function () {
        debugger
        var vm = this;
         vm.mode = this.$route.params.mode || 'Edit'; 
    console.log("Mode mila:", vm.mode);
        if(vm.reqType !== 'fromServiceMapping'){
            vm.configurationId = parseInt(this.$route.params.id);
            if(vm?.configurationId){
                this.tabsUnlocked = true;
            }
            // vm.reqFrom = vm.$route.query.reqFrom;
        }
        else if(vm.reqType === 'fromServiceMapping'){
            if(vm?.configurationId){
                this.tabsUnlocked = true;
            }
        }
    },
    methods: {
        callAddNew() {
            debugger
            if (this.$refs.overrideChargesRef && this.$refs.overrideChargesRef.AddNewRow) {
                this.$refs.overrideChargesRef.AddNewRow();
            }
        },
        // handleSavedConfig(payload) {
        //     this.configurationId = payload.configId;
        //     if (payload.goTo === 'NodeConfiguration') {
        //         const nextTab = this.tabs.find(tab => tab.title === this.$t('NodeConfiguration'));
        //         if (nextTab) {
        //             this.changeTab(nextTab);
        //         }
        //     }
        // },
        handleSavedConfig(payload) {
            this.configurationId = payload.configId;

            // Unlock all tabs
            this.tabsUnlocked = true;

            // Navigate to next tab if specified
            if (payload.goTo) {
                const nextTab = this.tabs.find(tab => tab.title === this.$t(payload.goTo));
                if (nextTab) {
                    this.currentTab = nextTab;
                }
            }
        },
        // changeTab(tab) {
        //     this.currentTab = tab;
        // }
        changeTab(tab) {
        // Allow navigation only to first tab if not unlocked
            if (!this.tabsUnlocked && tab.title !== this.$t('BillingRuleConfiguration')) {
                debugger;
                return;
            }
            this.currentTab = tab;
        }
    },
}
</script>
<style scoped>
.disabled-tab {
    cursor: not-allowed;
}
</style>