<template>
    <div>
      <div v-if="flowData.length > 0">
        <RFlowChart
          :data="flowData"
          chartElement="RFlowChart"
          chartClass="tree"
        />
      </div>
    </div>
  </template>
  
  <script>
  import RFlowChart from '../../../views/BillingRule/ConfigurationComponent/RFlowChart.vue';
  import DataService from "../../../services/DataService";
  
  export default {
    name: 'HorizontalFlowChart',
    components: { RFlowChart },
    props: {
      configurationId: {
        type: String,
        required: false
      }
    },
    data() {
      return {
        flowData: []
      };
    },
    created() {
      this.fetchRFlowData(this.configurationId);
    },
    methods: {
      async fetchRFlowData(configurationId) {
        const params = `configId=${configurationId}&includeParentNode=true&includeGlobalService=true`;
        const response = await DataService.GetNodeJsonForRule(params);
        if (response && response.data && response.data.length > 0) {
          this.flowData = this.buildTree(response.data);
        }
      },
      buildTree(flatData) {
        const idMap = {};
        const topLevelNodes = [];

        // Step 1: Create a map of nodes
        flatData.forEach(item => {
            idMap[item.clientServiceMappingId] = { ...item, children: [] };
        });

        // Step 2: Attach children to parents
        flatData.forEach(item => {
            const parentId = item.parentClientServiceMappingId;
            if (parentId && idMap[parentId]) {
            idMap[parentId].children.push(idMap[item.clientServiceMappingId]);
            } else {
            topLevelNodes.push(idMap[item.clientServiceMappingId]);
            }
        });

        // Step 3: Wrap all top-level nodes under a "Start" root
        const startNode = {
            serviceId: 'start',
            serviceName: 'Start',
            nodeName: 'Start',
            isParentNode: true,
            isLeafNode: false,
            condition: '',
            children: topLevelNodes
        };

        return [startNode];
     }
    }
  };
  </script>
  <style>
  .tree li, .tree ul { position: relative; transition: all .2s }
  * { margin: 0; padding: 0 }
  .tree ul { padding-top: 40px; -webkit-transition: all .2s; -moz-transition: all .2s }
  .tree li { float: left; text-align: center; list-style-type: none; padding: 41px 5px 0; -webkit-transition: all .2s; -moz-transition: all .2s }
  .tree li::after, .tree li::before, .tree ul ul::before { content: ''; position: absolute }
  .tree li::after, .tree li::before { top: 0; right: 50%; border-top: 2px solid #626262; width: 50%; height: 42px }
  .tree li::after { right: auto; left: 50%; border-left: 2px solid #626262 }
  .tree li:only-child::after, .tree li:only-child::before { display: none }
  .tree li:only-child { padding-top: 0 }
  .tree li:first-child::before, .tree li:last-child::after { border: 0 }
  .tree li:last-child::before { border-right: 2px solid #626262; border-radius: 0; -webkit-border-radius: 0; -moz-border-radius: 0 }
  .tree li:first-child::after { border-radius: 0; -webkit-border-radius: 0; -moz-border-radius: 0 } 
  .tree ul ul::before { top: 0px; left: 50%; border-left: 2px solid #626262; width: 0; height: 42px } 
  .tree li span {border:none !important;  padding:32px 10px 12px 10px; text-decoration: none; color: #666; font-family: arial, verdana, tahoma; font-size: 11px; display: inline-block; border-radius: 0; -webkit-border-radius: 0; -moz-border-radius: 0; transition: all .5s; -webkit-transition: all .5s; -moz-transition: all .5s; position: relative; width: 150px; background: #efefef; font-weight:600; min-height:116px; }
  .tree li span.grandparent { font-size: 12px; border-bottom: 2px solid #626262 !important; }
  .tree li span:hover, .tree li span:hover+ul li a { background: #c8e4f8; color: #000; border: 1px solid #94a0b4 }
  .tree li ul li:nth-child(4n+1) span{ border-bottom: 2px solid #7998d9 !important;}
  .tree li ul li:nth-child(4n+1) span:hover{ border-bottom: 2px solid #7998d9 !important; background:#dcf2fc !important; -webkit-transition:all .15s;transition:all .15s;-webkit-transform:translateY(-5px);transform:translateY(-5px) }
  .tree li ul li:nth-child(4n+1) span:hover::before{ color: #000;}
  .tree li ul li:nth-child(4n+2) span{ border-bottom: 2px solid #6cbfcf !important;}
  .tree li ul li:nth-child(4n+2) span:hover{ border-bottom: 2px solid #6cbfcf !important; background:#dcf2fc !important; -webkit-transition:all .15s;transition:all .15s;-webkit-transform:translateY(-5px);transform:translateY(-5px) }
  .tree li ul li:nth-child(4n+2) span:hover::before{ color: #000;}
  .tree li ul li:nth-child(4n+3) span{ border-bottom: 2px solid #e78a6b !important;}
  .tree li ul li:nth-child(4n+3) span:hover{ border-bottom: 2px solid #e78a6b !important; background:#dcf2fc !important; -webkit-transition:all .15s;transition:all .15s;-webkit-transform:translateY(-5px);transform:translateY(-5px)}
  .tree li ul li:nth-child(4n+3) span:hover::before{ color: #000;}
  .tree li ul li:nth-child(4n+4) span{ border-bottom: 2px solid #dbb06c !important;}
  .tree li ul li:nth-child(4n+4) span:hover{ border-bottom: 2px solid #dbb06c !important; background:#dcf2fc !important; -webkit-transition:all .15s;transition:all .15s;-webkit-transform:translateY(-5px);transform:translateY(-5px) }
  .tree li ul li:nth-child(4n+4) span:hover::before{ color: #000;}
  /*.tree li span.grandparent::after, .tree li span::after { color: #626262; content: "\25BA"; margin-left: 0px; width: 10px; transform: rotate(90deg); height: 10px; left: 50%; position: absolute; z-index: 10 }*/
  .tree li span:hover+ul li::after, .tree li span:hover+ul li::before, .tree li span:hover+ul ul::before, .tree li span:hover+ul::before { border-color: #94a0b4 }
  .tree li span::after { top: -10px }
  .tree li span.grandparent::after { top: 74px }
  .tree li span::before { color: #666; content: attr(data-content); height: 10px; left: 0; margin-left: 0; position: absolute; top: 7px; z-index: 10; font-weight: 700; width: 150px; font-size: 14px; text-align: center; }
  .tree li span.grandparent::before { content: "" }
  </style>